
import Toast from 'vux/src/components/toast/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Confirm from 'vux/src/components/confirm/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import scrollTop from '@/components/scrollTop.vue'
export default {
directives: {
TransferDom
},
components: {
Toast,
Confirm,
Flexbox,
FlexboxItem,
Checker,
CheckerItem,
XButton,
XInput,
Group,
XTextarea,
Icon,
scrollTop,
LoadMore
},
data () {
return {
type: '',
btnId: '',
confirmShow: false,
toastData: {
isShow: false,
type: 'success',
text: '订单已取消'
},
tabIndex: 0,
tabs: [
{
title: '全部订单',
status: 0
},
{
title: '代付款',
status: 1
},
{
title: '待发货',
status: 2
},
{
title: '待收货',
status: 4
},
{
title: '已完成',
status: 5
}
],
json: {
order: 'ASC',
pageNum: 1,
pageSize: 6,
status: '',
typeid: ''
},
orderUrl: 'order/orderlist',
lists: [],
loading: false,
isLastPage: false,
nofreight: localStorage.getItem('nofreight'),
freight: localStorage.getItem('freight')
}
},
created () {



let type = this.$route.params.type / 1
let id = this.$route.params.id / 1
let status = null
this.tabIndex = id
this.type = type
if (type === 1) { // 我的订单
this.orderUrl = 'order/orderlist'
this.json.typeid = 1
} else if (type === 2) { // 积分订单
this.orderUrl = 'order/orderintegrallist'
this.json.typeid = 1
} else if (type === 3) { // 试用订单
this.orderUrl = 'order/orderlist'
this.json.typeid = 3
}
switch (id) {
case 0:
status = 0
break
case 1:
status = 1
break
case 2:
status = 2
break
case 3:
status = 4
break
case 4:
status = 5
break
default:
break
}
this.getLists(status)
},
methods: {
tab (index) {
this.tabIndex = index
this.reset()
this.getLists(this.tabs[index].status)
},
getLists (status) {
this.json.status = status
this.axios.post(this.orderUrl, this.json).then((response) => {
if (response.success) {
let data = response.data
if (this.isLastPage) { return }
if (data.isLastPage) {
this.isLastPage = true
}
let lists = data.list
lists.map((item, index) => { // 订单状态: 0全部，1待付款，2已付款，3已配货，4已发货，5确认收货，10取消订单
switch (item.status / 1) {
case 1:
item.statusText = '待付款'
break
case 2:
item.statusText = '待发货'
break
case 3:
case 4:
item.statusText = '待收货'
break
case 5:
item.statusText = '已完成'
break
case 10:
item.statusText = '取消订单'
break
default:
break
}
if (item.orderprolist) {
console.log(item.orderprolist.length)
if (item.orderprolist.length > 0) {
item.orderprolist.map((item1, index1) => {
let type = '' 
if (item1.types === 0) {
type = ''
} else if (item1.types === 1) {
type = '满赠'
} else if (item1.types === 2) {
type = '买赠'
}
item1.type = type
item1.pic = this.base_img + item1.pic
})
}
} else { // 积分

if (item.status === status || status === 0) {
item.pic = this.base_img + item.propic
item.pid = item.proid
item.num = item.pronumber
item.ptitle = item.proname
item.amount = item.integral
item.orderprolist = [item]
} else {
lists = []
item.orderprolist = []
}
console.log(item)
}
})
if (this.json.pageNum / 1 === 1) {
this.lists = lists
} else {
this.lists = this.lists.concat(lists)
}
this.loading = false
this.json.pageNum++
}
})
console.log(this.lists)
},
loadMore () { // 上拉加载更多
if (this.isLastPage) {
return
}
this.loading = true
this.getLists(this.json.status)
},
cancle (id) {
this.confirmShow = true
this.btnId = id
console.log(id)
},
detailLink (id) {
this.$router.push({name: 'orderDetail', params: {type: this.type, id: id}})
console.log(id)
},
pay (id) {
console.log(id)
},
reset () {
this.lists = []
this.json.pageNum = 1
this.loading = false
this.isLastPage = false
},
onConfirm () { // order/ordercancel/
let id = this.btnId
this.axios.get('order/ordercancel/' + id).then((response) => {
if (response.success) {
this.toastData.isShow = true
this.lists.some((item, index) => {
if (item.id === id) {
this.lists.splice(index, 1)
}
})
}
})
console.log('on confirm', id)
}
}
}



